import React from 'react'
import { updateUserProfileSettings, getUserProfileThunkCreator } from '../../redux/profileReducer'
import { connect } from "react-redux";
import Settings from './Settings';


class SettingsContainer extends React.Component {

    componentDidMount() {
        this.props.getUserProfileThunkCreator(this.props.userId)
    }

    render() {
        return <>
            {
                !this.props.profile
                    ? null
                    : <Settings
                        sendProfileSettings={this.props.updateUserProfileSettings}
                        profile={this.props.profile}
                    />
            }

        </>
    }
}

let mapStateToProps = (state) => ({
    profile: state.profileReducer.profile,
    userId: state.authReducer.userId
})

export default connect(mapStateToProps, { updateUserProfileSettings, getUserProfileThunkCreator })(SettingsContainer) 