import React, { useState } from 'react'
import classes from './Settings.module.css'
import { Formik, Form, Field } from 'formik'
import FormikControl from '../Common/FormControls/FormikControl'
import { sendUserProfileSettings } from '../../API/api'
import Preloader from '../Common/Preloader/Preloader'


const Settings = (props) => {

    const { fullName, aboutMe, lookingForAJob, lookingForAJobDescription } = props.profile

    const radioOptions = [
        { key: 'Looking for a job', value: 'true' }
    ]

    const initialValues = {
        fullName: fullName,
        aboutMe: aboutMe,
        lookingForAJob: radioOptions,
        lookingForAJobDescription: lookingForAJobDescription,
    }

    const onSubmit = values => {
        const returnedData = { ...values, lookingForAJob: !!values.lookingForAJob[1] }
        props.sendProfileSettings(returnedData)
        console.log(returnedData)
    }
    
    return (
        <>
            <Formik initialValues={initialValues} onSubmit={onSubmit}>
                {
                    formik => {
                        return <Form>
                            <h1>Profile settings</h1>
                            <FormikControl
                                control='input'
                                label='Full Name'
                                name='fullName'
                            />
                            <FormikControl
                                control='input'
                                label='About me'
                                name='aboutMe'
                            />
                            <FormikControl
                                control='input'
                                label='My skills'
                                name='lookingForAJobDescription'
                            />
                            <FormikControl
                                control='checkbox'
                                name='lookingForAJob'
                                options={radioOptions} />
                            <button>Save</button>
                        </Form>
                    }
                }
            </Formik>

        </>
    )
}

export default Settings